@import '../../../styles/Responsive.scss';

.usercard {
    width: 70%;
    overflow: hidden;
    border-radius: 2rem;

    box-shadow: 3px 5px 3px 2px rgba(0, 0, 0, 0.25), -3px 5px 3px 2px rgba(0, 0, 0, 0.25);

    @include media(pc) {
        width: 35%;
    }

    img {
        width: 45%;
    }

    .numbers {
        width: 16rem;
    }
}